"use client";
import { useEffect, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";
import { CompanyService } from "@/services/company.service";
import toast from "react-hot-toast";

/* =========================
   Types (FIXED NAMES)
========================= */
export interface ProductFormData {
  categoryId: string;
  categoryTypeId: string;
  branchId: string;
  quantity: string;
  minStockLevel: string;
  name: string;
  description: string;
  unit: string;
  costPrice: string;
  wholesalePrice: string;
  sellPrice: string; // Added sellPrice
}

interface Option {
  id: string;
  name: string;
}

interface Branch {
  id: string;
  branchName: string;
}

interface Props {
  open: boolean;
  mode: "add" | "edit";
  initialData?: ProductFormData;
  onClose: () => void;
  onSubmit: (data: ProductFormData) => void;
}

const emptyForm: ProductFormData = {
  categoryId: "",
  categoryTypeId: "",
  branchId: "",
  quantity: "",
  minStockLevel: "",
  name: "",
  description: "",
  unit: "",
  costPrice: "",
  wholesalePrice: "",
  sellPrice: "", // Added sellPrice
};

export default function ProductFormModal({
  open,
  mode,
  initialData,
  onClose,
  onSubmit,
}: Props) {
  const [form, setForm] = useState<ProductFormData>(emptyForm);
  const [loading, setLoading] = useState(false);
  const [categories, setCategories] = useState<Option[]>([]);
  const [categoryTypes, setCategoryTypes] = useState<Option[]>([]);
  const [branches, setBranches] = useState<Branch[]>([]);

  /* =========================
     LOAD DROPDOWNS (FIXED)
  ========================= */
  useEffect(() => {
    if (!open) return;

    Promise.all([
      CompanyService.getAllCategory(),
      CompanyService.getAllCategoryTypes(),
      CompanyService.getAllBranches(),
    ])
      .then(([c, ct, b]) => {
        setCategories(c.data.data || []);
        setCategoryTypes(ct.data.data || []);
        setBranches(b.data.data || []);
      })
      .catch(() => toast.error("Failed to load dropdown data"));
  }, [open]);

  /* =========================
     INIT FORM
  ========================= */
  useEffect(() => {
    setForm(mode === "edit" && initialData ? initialData : emptyForm);
  }, [mode, initialData, open]);

  const update = (k: keyof ProductFormData, v: string) =>
    setForm((p) => ({ ...p, [k]: v }));

  const validate = () => {
    if (!form.name) return toast.error("Product name is required"), false;
    if (!form.categoryId) return toast.error("Category is required"), false;
    if (!form.categoryTypeId) return toast.error("Category type is required"), false;
    if (!form.branchId) return toast.error("Branch is required"), false;
    return true;
  };

  const submit = async () => {
    if (!validate() || loading) return;
    setLoading(true);
    await onSubmit(form);
    setLoading(false);
  };

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 backdrop-blur-sm transition-opacity duration-200">
      <div className="w-full max-w-4xl mx-4 bg-white dark:bg-gray-900 rounded-2xl shadow-2xl overflow-hidden border border-gray-200 dark:border-gray-800 animate-in zoom-in-95 duration-200">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-gray-100 dark:border-gray-800">
          <div className="flex items-center gap-3">
            <div className="p-2 bg-blue-50 dark:bg-blue-900/30 rounded-lg">
              <svg className="w-6 h-6 text-blue-600 dark:text-blue-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
              </svg>
            </div>
            <div>
              <h3 className="text-xl font-semibold text-gray-900 dark:text-white">
                {mode === "add" ? "Add New Product" : "Edit Product"}
              </h3>
              <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                {mode === "add" ? "Fill in the details to add a new product" : "Update the product information"}
              </p>
            </div>
          </div>
          <button
            onClick={onClose}
            className="p-2 hover:bg-gray-100 dark:hover:bg-gray-800 rounded-lg transition-colors duration-200"
          >
            <svg className="w-5 h-5 text-gray-500 dark:text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        {/* Body */}
        <div className="p-6 max-h-[60vh] overflow-y-auto">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            
            {/* LEFT COLUMN */}
            <div className="space-y-6">
              <Field label="Product Name" required>
                <Input
                  value={form.name}
                  onChange={(e) => update("name", e.target.value)}
                  placeholder="Enter product name"
                  className="w-full"
                />
              </Field>

              <div className="grid grid-cols-2 gap-4">
                <Field label="Quantity">
                  <Input
                    type="number"
                    value={form.quantity}
                    onChange={(e) => update("quantity", e.target.value)}
                    placeholder="0"
                    min="0"
                  />
                </Field>

                <Field label="Min Stock Level">
                  <Input
                    type="number"
                    value={form.minStockLevel}
                    onChange={(e) => update("minStockLevel", e.target.value)}
                    placeholder="0"
                    min="0"
                  />
                </Field>
              </div>

              <Field label="Category" required>
                <select
                  className="w-full px-3 py-2.5 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition-all duration-200"
                  value={form.categoryId}
                  onChange={(e) => update("categoryId", e.target.value)}
                >
                  <option value="" className="text-gray-400">Select Category</option>
                  {categories.map((c) => (
                    <option key={c.id} value={c.id} className="py-2">
                      {c.name}
                    </option>
                  ))}
                </select>
              </Field>

              <Field label="Category Type" required>
                <select
                  className="w-full px-3 py-2.5 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition-all duration-200"
                  value={form.categoryTypeId}
                  onChange={(e) => update("categoryTypeId", e.target.value)}
                >
                  <option value="" className="text-gray-400">Select Category Type</option>
                  {categoryTypes.map((ct) => (
                    <option key={ct.id} value={ct.id} className="py-2">
                      {ct.name}
                    </option>
                  ))}
                </select>
              </Field>
            </div>

            {/* RIGHT COLUMN */}
            <div className="space-y-6">
              <Field label="Branch" required>
                <select
                  className="w-full px-3 py-2.5 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition-all duration-200"
                  value={form.branchId}
                  onChange={(e) => update("branchId", e.target.value)}
                >
                  <option value="" className="text-gray-400">Select Branch</option>
                  {branches.map((b) => (
                    <option key={b.id} value={b.id} className="py-2">
                      {b.branchName}
                    </option>
                  ))}
                </select>
              </Field>

              <div className="grid grid-cols-2 gap-4">
                <Field label="Unit">
                  <select
                    className="w-full px-3 py-2.5 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition-all duration-200"
                    value={form.unit}
                    onChange={(e) => update("unit", e.target.value)}
                  >
                    <option value="" className="text-gray-400">Select Unit</option>
                    <option value="kg" className="py-2">Kg</option>
                    <option value="bag" className="py-2">Bag</option>
                    <option value="box" className="py-2">Box</option>
                  </select>
                </Field>
              </div>

              {/* PRICE SECTION - Updated to 3 columns */}
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <Field label="Cost Price">
                  <div className="relative">
                    <span className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-500 dark:text-gray-400">
                      $
                    </span>
                    <Input
                      type="number"
                      value={form.costPrice}
                      onChange={(e) => update("costPrice", e.target.value)}
                      placeholder="0.00"
                      min="0"
                      className="pl-8 w-full"
                    />
                  </div>
                </Field>

                <Field label="Wholesale Price">
                  <div className="relative">
                    <span className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-500 dark:text-gray-400">
                      $
                    </span>
                    <Input
                      type="number"
                      value={form.wholesalePrice}
                      onChange={(e) => update("wholesalePrice", e.target.value)}
                      placeholder="0.00"
                      min="0"
                      className="pl-8 w-full"
                    />
                  </div>
                </Field>

                <Field label="Sell Price">
                  <div className="relative">
                    <span className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-500 dark:text-gray-400">
                      $
                    </span>
                    <Input
                      type="number"
                      value={form.sellPrice}
                      onChange={(e) => update("sellPrice", e.target.value)}
                      placeholder="0.00"
                      min="0"
                      className="pl-8 w-full"
                    />
                  </div>
                </Field>
              </div>

              <Field label="Description">
                <textarea
                  value={form.description}
                  onChange={(e) => update("description", e.target.value)}
                  placeholder="Enter product description (optional)"
                  rows={3}
                  className="w-full px-3 py-2.5 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-700 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none resize-none transition-all duration-200"
                />
              </Field>
            </div>
          </div>
        </div>

        {/* Footer */}
        <div className="p-6 border-t border-gray-100 dark:border-gray-800 bg-gray-50/50 dark:bg-gray-900/50">
          <div className="flex items-center justify-between">
            <div className="text-sm text-gray-500 dark:text-gray-400">
              Fields marked with <span className="text-red-500">*</span> are required
            </div>
            <div className="flex gap-3">
              <Button
                variant="outline"
                onClick={onClose}
                className="px-6 py-2.5 border-gray-300 dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-800 transition-colors duration-200"
              >
                Cancel
              </Button>
              <Button
                onClick={submit}
                disabled={loading}
                className="px-6 py-2.5 bg-blue-600 hover:bg-blue-700 text-white font-medium rounded-lg transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2"
              >
                {loading ? (
                  <>
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin" />
                    Saving...
                  </>
                ) : (
                  <>
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                    </svg>
                    {mode === "add" ? "Add Product" : "Update Product"}
                  </>
                )}
              </Button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

/* =========================
   Field Helper Component
========================= */
function Field({ label, children, required, className = "" }: { label: string; children: React.ReactNode; required?: boolean; className?: string }) {
  return (
    <div className={`space-y-2 ${className}`}>
      <Label className="flex items-center gap-1 text-sm font-medium text-gray-700 dark:text-gray-300">
        {label}
        {required && <span className="text-red-500">*</span>}
      </Label>
      {children}
    </div>
  );
}