"use client";

import React, { useEffect, useRef, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import ConfirmDeleteModal from "./ConfirmDeleteModal";
import { CompanyService } from "@/services/company.service";
import toast from "react-hot-toast";
import ProductFormModal, { ProductFormData } from "./ProductFormModal";

/* =========================
   Types
========================= */
interface ProductDto {
  id: string;
  name: string;
  description: string;

  categoryId: string;
  categoryName: string;

  categoryTypeId: string;
  categoryTypeName: string;

  branchId: string;
  branchName: string;

  quantity: string;
  unit: string;
  costPrice: string;
    sellPrice: string;
  wholesalePrice: string;
    minStockLevel: string;

  createdAt: string;
  createdBy: string;
}

/* =========================
   Action Menu (UNCHANGED)
========================= */
function ActionMenu({
  openUp,
  onEdit,
  onDelete,
}: {
  openUp?: boolean;
  onEdit: () => void;
  onDelete: () => void;
}) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    function handleClickOutside(e: MouseEvent) {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    }
    if (open) document.addEventListener("mousedown", handleClickOutside);
    return () =>
      document.removeEventListener("mousedown", handleClickOutside);
  }, [open]);

  return (
    <div ref={ref} className="relative">
      <button
        onClick={() => setOpen((p) => !p)}
        className="rounded-full p-2 hover:bg-gray-100 dark:hover:bg-gray-700"
      >
        ‚ãÆ
      </button>

      {open && (
        <div
          className={`absolute right-0 z-50 w-36 rounded-lg border bg-white shadow-lg
          dark:border-gray-700 dark:bg-gray-900
          ${openUp ? "bottom-full mb-2" : "top-full mt-2"}`}
        >
          <button
            onClick={() => {
              onEdit();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm hover:bg-gray-50 dark:hover:bg-gray-800"
          >
            ‚úèÔ∏è Edit
          </button>

          <button
            onClick={() => {
              onDelete();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20"
          >
            üóë Delete
          </button>
        </div>
      )}
    </div>
  );
}

/* =========================
   Product Table
========================= */
export default function ProductTable() {
  const [products, setProducts] = useState<ProductDto[]>([]);
  const [search, setSearch] = useState("");

  const [openModal, setOpenModal] = useState(false);
  const [mode, setMode] = useState<"add" | "edit">("add");
  const [selectedProduct, setSelectedProduct] = useState<ProductDto | null>(null);

  const [openDelete, setOpenDelete] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const [loading, setLoading] = useState(false);

  /* =========================
     LOAD PRODUCTS
  ========================= */
async function loadProducts() {
  setLoading(true);
  try {
    const res = await CompanyService.getAllProducts();

    // ‚úÖ CORRECT: use items array
    setProducts(res.data.data.items || []);
  } catch {
    toast.error("Failed to load products");
    setProducts([]);
  } finally {
    setLoading(false);
  }
}

  useEffect(() => {
    loadProducts();
  }, []);

  /* =========================
     ADD / UPDATE
  ========================= */
  async function handleSubmit(data: ProductFormData) {
    try {
      if (mode === "add") {
        await CompanyService.CreateProductAsync?.(data);
        toast.success("Product created successfully");
      } else {
        if (!selectedProduct?.id) {
          toast.error("Product ID missing");
          return;
        }

        // await CompanyService.updateProduct?.(selectedProduct.id, data);
        // toast.success("Product updated successfully");
      }

      setOpenModal(false);
      loadProducts();
    } catch {
      toast.error("Operation failed");
    }
  }

  /* =========================
     DELETE
  ========================= */
  async function confirmDelete() {
    if (!selectedProduct) return;

    try {
      setDeleting(true);
      // await CompanyService.deleteProduct?.(selectedProduct.id);
      // toast.success("Product deleted successfully");
      setOpenDelete(false);
      loadProducts();
    } catch {
      toast.error("Failed to delete product");
    } finally {
      setDeleting(false);
    }
  }

  /* =========================
     FILTER
  ========================= */
  const filteredProducts = products.filter((p) =>
    p.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="w-full rounded-xl bg-white px-6 pb-6 dark:bg-gray-900">
      <h2 className="mb-4 text-xl font-semibold text-gray-800 dark:text-white">
        Products
      </h2>

      {/* Top Bar */}
      <div className="mb-4 flex items-center justify-between">
        <Input
          placeholder="Search products..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-64"
        />
        <Button
          size="sm"
          onClick={() => {
            setMode("add");
            setSelectedProduct(null);
            setOpenModal(true);
          }}
        >
          + Add Product
        </Button>
      </div>

      {/* Table */}
      <div className="overflow-x-auto">
        <table className="w-full border-collapse">
          <thead className="bg-gray-50 dark:bg-gray-800">
            <tr className="text-left text-sm text-gray-600 dark:text-gray-300">
              <th className="px-3 py-3">Name</th>
              <th className="px-3 py-3">Category</th>
              <th className="px-3 py-3">Type</th>
              <th className="px-3 py-3">Unit</th>
              <th className="px-3 py-3">Cost</th>
              <th className="px-3 py-3">selling</th>
              <th className="px-3 py-3">Wholesale</th>
              <th className="px-3 py-3">Qty</th>
              <th className="px-3 py-3">Created At</th>
              <th className="px-3 py-3 text-right">Action</th>
            </tr>
          </thead>

          <tbody>
            {loading ? (
              <tr>
                <td colSpan={7} className="py-6 text-center text-sm">
                  Loading products...
                </td>
              </tr>
            ) : (
              filteredProducts.map((p, i) => (
                <tr
                  key={p.id}
                  className="border-t hover:bg-gray-50 dark:hover:bg-gray-800 transition"
                >
                  <td className="px-3 text-sm">{p.name}</td>
                  <td className="px-3 text-sm">{p.categoryName}</td>
                  <td className="px-3 text-sm">{p.categoryTypeName}</td>
                  <td className="px-3 text-sm">{p.unit}</td>
                  <td className="px-3 text-sm text-red-500">${p.costPrice}</td>
                  <td className="px-3 text-green-600">${p.sellPrice}</td>

                  <td className="px-3 text-blue-600">${p.wholesalePrice}</td>
<td
  className={`px-3 text-sm font-medium ${
    parseInt(p.quantity, 10) < 10
      ? "bg-red-100 text-red-700"
      : "bg-green-100 text-green-700"
  }`}
>
  {p.quantity}
</td>
                  <td className="px-3 text-sm">
                    {new Date(p.createdAt).toISOString().split("T")[0]}
                  </td>

                  <td className="px-3 text-right">
                    <ActionMenu
                      openUp={i === filteredProducts.length - 1}
                      onEdit={() => {
                        setMode("edit");
                        setSelectedProduct(p);
                        setOpenModal(true);
                      }}
                      onDelete={() => {
                        setSelectedProduct(p);
                        setOpenDelete(true);
                      }}
                    />
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/* ADD / EDIT MODAL */}
      <ProductFormModal
        open={openModal}
        mode={mode}
        initialData={
          selectedProduct
            ? {
                name: selectedProduct.name,
                description: selectedProduct.description,
                categoryId: selectedProduct.categoryId,
                categoryTypeId: selectedProduct.categoryTypeId,
                branchId: selectedProduct.branchId,
                quantity: selectedProduct.quantity,
                unit: selectedProduct.unit,
                costPrice: selectedProduct.costPrice,
                wholesalePrice: selectedProduct.wholesalePrice,
                minStockLevel: selectedProduct.minStockLevel,
                sellPrice: selectedProduct.sellPrice,

              }
            : undefined
        }
        onClose={() => setOpenModal(false)}
        onSubmit={handleSubmit}
      />

      {/* DELETE CONFIRM */}
      <ConfirmDeleteModal
        open={openDelete}
        loading={deleting}
        onClose={() => setOpenDelete(false)}
        onConfirm={confirmDelete}
      />
    </div>
  );
}
